.f {
  align-items: center;
}
.row {
  --bs-gutter-x: 0;
}
.nav-item {
  margin-left: 2vw;
  margin-right: 2vw;
}
.navbar {
  margin: 0 !important;
}
#nav1 {
  z-index: 200;
  background-image: linear-gradient();
}
#nav-2 {
  position: sticky !important;
  top: 0 !important;
  z-index: 200 !important;
}
#nav1 .nav-link {
  border-bottom: 0.1em solid white;
  padding: 1rem 2rem !important;

  border-radius: 50px;
}
.nav-links:hover {
  background-color: whitesmoke;
  border-radius: 50px;
  color: rgb(73, 16, 126);
}
.nav-links:focus {
  color: red;
}
#close {
  color: rgba(0, 0, 0, 0.55);
  font-size: xx-large;
}
.navbar-toggler:focus {
  box-shadow: 0;
}

.navbar-brand {
  text-align: center;
  flex: 5;
}
.nav-link {
  border-bottom: 0.2em solid rgb(248, 248, 248);
  font-size: x-large;
  padding: 1.5vw !important;
}
.nav-link:hover {
  border-bottom: 0.2em solid red;
}

@media (max-width: 540px) {
  #nav1 {
    overflow: auto;
    white-space: nowrap;
    scroll-behavior: auto;
  }
  #nav1 .nav-links {
    display: inline-block;

    text-align: center;
    padding: 14px;
    text-decoration: none;
    font-size: 1.25rem !important;
    padding: 1.25rem !important;
  }

  .nav {
    flex-wrap: nowrap !important;
  }
}
