.cart {
  color: green;
  margin-right: 2vw;
  font-size: xx-large !important;
  position: relative;
  bottom: 1vh !important;
  cursor: pointer;
}
.cart:hover {
  color: rgb(158, 158, 37);
}
.img {
  height: 15rem !important;
}
/* .card {
  filter: brightness(1);
  width: 30% !important;
  margin-left: 1vw;
  margin-right: 1vw;
  margin-bottom: 3vh;
} */

.card:hover {
  filter: brightness(1.02);
  font-size: xx-large !important;
}
.price {
  display: flex !important;
  margin-top: 3vh;
  justify-content: space-between !important;
  align-items: center !important;
}
.price h4 {
  flex: 0.8;
  text-align: center;
}

.price h3 {
  color: rgb(194, 21, 21);
  margin-right: 1.5vw;
}
.wish {
  margin-right: 3vw;
  color: rgba(0, 0, 0, 0.55);
  font-size: xx-large !important;
  position: relative;
  bottom: 1vh !important;
  cursor: pointer;
}
.wish:hover {
  color: rgb(216, 35, 35);
}
.col-sm-6 {
  width: auto;
}
.col-lg-4 {
  width: 30% !important;
  margin: 2vh 1vw !important;
}
.k {
  padding-left: 2.5vw !important;
}

@media (min-width: 1417px) and (max-width: 1514px) {
  .card-bar {
    width: 26.5rem !important;
  }
  .card-bar img {
    height: 21rem !important;
  }
  .k {
    align-items: center !important;
    justify-content: center;
  }
  .price {
    height: 5rem !important;
    font-size: 1.5rem !important;
  }
}
@media (min-width: 1515px) {
}
.k {
  background-image: linear-gradient(to bottom, white, red);
}
