.wish {
  margin-right: 3vw;
  color: rgba(0, 0, 0, 0.55);
  font-size: xx-large !important;
  position: relative;
  bottom: 1vh !important;
}
.wish:hover {
  color: rgb(216, 35, 35);
}

@media (max-width: 768px) {
  .card {
    justify-content: space-between !important;
  }
}
.dishes .img {
  height: 25vh !important;
}

