.card:hover {
  filter: brightness(1.02);
  font-size: xx-large !important;
}
.price {
  display: flex !important;
  margin-top: 3vh;
  justify-content: space-between !important;
  align-items: center !important;
}
.price h4 {
  flex: 0.8;
  text-align: center;
}

.price h3 {
  color: rgb(194, 21, 21);
  margin-right: 1.5vw;
}
.cart {
  color: green;
  margin-right: 2vw;
  font-size: xx-large !important;
  position: relative;
  bottom: 1vh !important;
}
.cart:hover {
  color: rgb(158, 158, 37);
}
