#signup_div .card:first-child {
  margin-right: 0vw;

  width: 45vw !important;
  height: 70vh;
  border-radius: 0 !important;
  margin-left: 5vw;
}
.carousel-item {
  margin-left: 0 !important;

  width: 45vw !important;
  margin-right: 0 !important;
  border-radius: 0 !important;
}
#signup_div .card-body:first-child {
  padding: 0 !important;
}
#signup {
  display: flex;
}
#signup_div {
  background-color: whitesmoke;
  margin: 0 !important;
  margin-bottom: 0 !important;
}

#signup_div .card {
  border: 0;
  margin-top: 15vh;
  margin-bottom: 15vh;
}
#signup_div .card-title {
  text-align: center;
  margin-top: 5vh;
  font-size: x-large;
}
#signup_div .mink {
  font-size: 1rem;
  padding: 1vh 1vw !important;

  border-radius: 100px;
  align-items: center !important;
  text-align: center;
  width: 80%;
  margin-left: 10%;
  margin-top: 2vh;
  transition: 0.5s;
  text-decoration: none;
  background-color: whitesmoke;
}

@media (min-width: 611px) and (max-width: 768px) {
  #signup_div .card-title {
    font-size: 1.5rem;
  }
  #signup_div .mink {
    font-size: medium;
    width: 80%;
    margin-left: 5vw !important;
  }
  #signup_div .card:first-child {
    margin-right: 0 !important;
    margin-left: 5vw !important;
  }
  #signup_div .card:first-child .form-label {
    margin-left: 2vw !important;
  }
  #signup_div .card:first-child .form-control {
    margin-left: 2.5vw !important;
    width: 40vw !important;
  }
  #signup_div .card:first-child button {
    margin-top: 3vh !important;
    width: 15vw !important;
    border-radius: 100px;
  }
  #forget {
    margin-right: 2vw !important;
    font-size: 1rem !important;
  }
}
@media (max-width: 610px) {
  #signup {
    display: block;
  }

  #signup_div .card:first-child {
    width: 97vw !important;
    height: 70vh !important;
    margin-bottom: -15vh !important;
    margin-left: 0 !important;
    margin-top: 0 !important;
  }
  #signup_div .carousel-item img {
    width: 97vw !important;

    margin-left: 0 !important;
  }
  #signup_div .card:first-child .form-control {
    margin-left: 13vw !important;
    width: 80vw !important;
    margin-right: 0vw;
    margin-bottom: 3.5vh !important;
  }
  #signup_div .card:first-child .nav-link {
    font-size: 1rem !important;
  }
  #signup_div .card:first-child button {
    margin-top: 3vh;
    width: 20vw !important;
    border-radius: 100px;
  }
  #signup_div .card:first-child .form-label {
    margin-left: 10vw;
  }
  #forget {
    margin-right: 5vw !important;
  }
}

@media (min-width: 1200px) and (max-width: 1400px) {
  #signup_div .card-title {
    font-size: 2rem;
  }
  #signup_div .mink {
    font-size: medium;
    width: 60%;
    margin-left: 8vw !important;
  }
  #signup_div .card:first-child {
    margin-right: 0 !important;
    margin-left: 5vw !important;
  }
  #signup_div .card:first-child .form-label {
    margin-left: 8vw !important;
  }
  #signup_div .card:first-child .form-control {
    margin-left: 10vw !important;
    width: 25vw !important;
  }
  #signup_div .card:first-child button {
    margin-top: 3vh !important;
    width: 10vw !important;
    height: 7vh !important;
  }
  #forget {
    text-align: center !important;
    margin-left: 13vw !important;
  }
}

@media (min-width: 1400px) and (max-width: 1600px) {
  #signup_div .card-title {
    font-size: 2rem;
  }
  #signup_div .card {
    width: 40vw !important;
    margin-left: 8vw !important;
  }
  .carousel-item img {
    width: 40vw !important;
  }
  #signup_div .mink {
    font-size: medium;
    width: 55%;
    margin-left: 10vw !important;
  }

  #signup_div .card:first-child .form-label {
    margin-left: 10vw !important;
  }
  #signup_div .card:first-child .form-control {
    margin-left: 11.5vw !important;
    width: 23vw !important;
  }
  #signup_div .card:first-child button {
    margin-top: 3vh !important;
    width: 9vw !important;
    height: 7vh !important;
  }
  #forget {
    text-align: center !important;
    margin-left: 16.5vw !important;
  }
}

@media (min-width: 1600px) {
  #signup_div .card-title {
    font-size: 2rem;
  }
  #signup_div .card {
    width: 35vw !important;
    margin-left: 7vw !important;
  }
  .carousel-item img {
    width: 40vw !important;
  }
  #signup_div .mink {
    font-size: medium;
    width: 55%;
    margin-left: 10vw !important;
  }

  #signup_div .card:first-child .form-label {
    margin-left: 10vw !important;
  }
  #signup_div .card:first-child .form-control {
    margin-left: 11.5vw !important;
    width: 23vw !important;
  }
  #signup_div .card:first-child button {
    width: 9vw !important;
    height: 7vh !important;
  }
  #forget {
    text-align: center !important;
    margin-left: 18vw !important;
  }
}
#signup_div .mink:hover {
  background-color: lightgray;
  border-bottom: none !important;
}

#signup_div .mink img {
  position: relative !important;
  bottom: 0.5vh !important;
  margin-right: 0.5vw;
}
#signup_div .card-text {
  text-align: center;
  margin-top: 2vh;
  font-size: 1rem !important;
}
#signup_div .form-control {
  margin-left: 2vw !important;
  margin-right: 2vw !important;
  width: 90%;
  outline: none !important;
  outline-color: white;
  outline-width: 0px;
}
#signup_div .form-label {
  margin-left: 1vw;
}
#signup_div .form-group {
  margin-bottom: 1vh;
  font-size: 1rem !important;
}
#signup_div button {
  margin: 0 auto;
  margin-top: 3vh;
  display: block !important;
  align-self: center;
  font-size: 1rem !important;
  margin-bottom: 0.5rem;
  width: 12vw !important;
  border-radius: 100px;
}
.nav h6 {
  margin-left: -2vw !important;
  margin-right: 0.5vw !important;
  margin-top: 2vh;
}

/*carousel*/
#signup_div .navv {
  margin-top: 1.5vh;
  text-decoration: none;
  color: rgb(185, 0, 0);
  font-size: 1rem !important;
}
.nav-mar {
  justify-content: center !important;
}

.carousel-item img {
  margin-top: 15vh;
  width: 100%;
  height: 70vh;
}
#forget {
  font-size: 1.15rem;
  margin-right: 2vw !important;
  margin-top: 1rem !important;
  text-align: right;
  font-weight: bolder;
  color: green;
  cursor: pointer;
}
#forget:hover {
  color: rgb(33, 95, 17);
}
.submit1 {
  margin-top: 5rem !important;
}
.form {
  margin-top: 20vh;
}
.goto {
  margin-left: 18vw;
  margin-top: 5vh;
}
